// stylelint-disable

html,
.login-page {
  height: 100%;
}

.login-main {
	margin-top: 80px;	

	@media screen and (max-width: 767px) {
		margin-top: 0;
	}
}

.login-footer {
	background: $white;
	border-top: 1px solid $body-bg;

	[class*="col-"]:last-child {
		text-align: right;
	}

	@media screen and (max-width: 767px) {
		text-align: center;

		[class*="col-"]:last-child {
			text-align: center;
		}
	}
}