// stylelint-disable
// Bootstrap overrides

//
// Color system
//
$gray-base:  #181b1e !default;
$white:      #ffffff !default;
$gray-100:   #f8f9fa !default;
$gray-200:   #e9ecef !default;
$gray-300:   #dee2e6 !default;
$gray-400:   #ced4da !default;
$gray-500:   #adb5bd !default;
$gray-600:   #6c757d !default;
$gray-700:   #495057 !default;
$gray-800:   #343a40 !default;
$gray-900:   #212529 !default;
$black:      #000 !default;

$blue:       #007bff !default;
$indigo:     #6610f2 !default;
$purple:     #6f42c1 !default;
$pink:       #e83e8c !default;
$red:        #dc3545 !default;
$orange:     #fd7e14 !default;
$yellow:     #ffc107 !default;
$green:      #28a745 !default;
$teal:       #20c997 !default;
$cyan:       #17a2b8 !default;
$light-blue: #63c2de !default;

$colors: () !default;
$colors: map-merge((
  "light-blue":  $light-blue,
  "gray-100": $gray-100,
  "gray-200": $gray-200,
  "gray-300": $gray-300,
  "gray-400": $gray-400,
  "gray-500": $gray-500,
  "gray-600": $gray-600,
  "gray-700": $gray-700,
  "gray-800": $gray-800,
  "gray-900": $gray-900
), $colors);

$primary:       $blue !default;
$secondary:     $gray-600 !default;
$success:       $green !default;
$info:          $cyan !default;
$warning:       $yellow !default;
$danger:        $red !default;
$light:         $gray-100 !default;
$dark:          $gray-800 !default;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-transitions:              true !default;
// $enable-rounded:                  false !default;

// Body
//
// Settings for the `<body>` element.

$body-bg:                         #f4f6f9 !default;

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-gutter-width:               15px !default;

// Components
//
// Define common padding and border radius sizes and more.

$border-color:                    $gray-300 !default;
$border-radius:                   .25rem !default;

// Typography
//
// Font, line-height, and color for body text, headings, and more.

$font-size-base:                  .875rem !default;

// Breadcrumbs

// $breadcrumb-bg:                   #fff !default;
$breadcrumb-bg:                   transparent !default;
// $breadcrumb-margin-bottom:        1.5rem !default;
$breadcrumb-margin-bottom:        0 !default;
$breadcrumb-border-radius:        0 !default;

// Cards

$card-spacer-y:                   .75rem !default;
$card-spacer-x:                   1.25rem !default;
$card-border-width:               0 !default; //$border-width !default;
$card-border-radius:              $border-radius !default;
$card-border-color:               rgba($black, .125) !default;
$card-inner-border-radius:        calc(#{$card-border-radius} - #{$card-border-width}) !default;
$card-cap-bg:                     rgba($black, .03) !default;
$card-bg:                         $white !default;

$card-img-overlay-padding:        1.25rem !default;

$card-group-margin:               ($grid-gutter-width / 2) !default;
$card-deck-margin:                $card-group-margin !default;

$card-columns-count:              3 !default;
$card-columns-gap:                1.25rem !default;
$card-columns-margin:             $card-spacer-y !default;

// Dropdowns

$dropdown-padding-y:              0 !default;
$dropdown-border-color:           $gray-300 !default;
$dropdown-divider-bg:             $gray-200 !default;

// Buttons

$btn-secondary-border:            $gray-300 !default;

// Progress bars

$progress-bg:                     $gray-100 !default;

// Tables

$table-bg-accent:                 $gray-100 !default;
$table-bg-hover:                  $gray-100 !default;

// Forms

$input-group-addon-bg:            $gray-100 !default;
$input-border-color:              $gray-200 !default;
$input-group-addon-border-color:  $gray-200 !default;
