// stylelint-disable
.nav-links {
	display: flex;
	box-shadow: 0 0 0 1px #e5e5e5;
	border-top-right-radius: 4px;
	border-top-left-radius: 4px;

	.nav-item {
		flex: 1;
		margin-bottom: 0;
		text-align: center;
		border-left: 1px solid #e5e5e5;

		&:first-of-type {
			border-left: 0;
			border-top-left-radius: 4px;
		}

		&:last-of-type {
			border-top-right-radius: 4px;
		}

		&:not(.active) {
			background-color: #fafafa;
		}

		a {
			padding: 10px;
			line-height: 28px;
			color: #707070;
			border-bottom: 2px solid transparent;
			white-space: nowrap;
			width: 100%;

			&:hover {
				text-decoration: none;
				color: #000;
				border-bottom: 2px solid #c4c4c4;
			}
		}
	}

	.nav-link {
		border-top: 0;
		border-left: 0;
		border-right: 0;
	}    
}

.tab-boxs {
	margin-top: 0;
	background: #fff;
	border: 0;
}

.tab-box {
	box-shadow: 0 0 0 1px #e5e5e5;
	border-bottom-right-radius: 2px;
	border-bottom-left-radius: 2px;
	padding: 15px;
}

body.ui-indigo .nav-links .nav-link.active {
	border-bottom: 2px solid #6666c4;
	font-weight: 600;
}